import React, {Component} from 'react';
import {withTranslation} from "react-i18next";
import './progress.component.css';
// import { Line  } from '@reactchartjs/react-chart.js';

class progressWeightComponent extends Component {

    constructor(props) {
        super(props);
    }
    render() {
        const {t} = this.props;
        const data = {
            labels: ['1', '2', '3', '4', '5', '6'],
            datasets: [
                {
                    label: '# of Votes',
                    data: [12, 19, 3, 5, 2, 3],
                    fill: false,
                    backgroundColor: 'rgb(255, 99, 132)',
                    borderColor: 'rgba(255, 99, 132, 0.2)',
                },
            ],
        }

        const options = {
            scales: {
                yAxes: [
                    {
                        ticks: {
                            beginAtZero: true,
                        },
                    },
                ],
            },
        }
        return (
            <>
                Weight
                {/*<div className='header'>*/}
                {/*    <h1 className='title'>Line Chart</h1>*/}
                {/*    <div className='links'>*/}
                {/*        <a*/}
                {/*            className='btn btn-gh'*/}
                {/*            href='https://github.com/reactchartjs/react-chartjs-2/blob/react16/example/src/charts/Line.js'*/}
                {/*        >*/}
                {/*            Github Source*/}
                {/*        </a>*/}
                {/*    </div>*/}
                {/*</div>*/}
                {/*<Line data={data} options={options} />*/}

            </>
        );
    }
}

export default withTranslation('translation') (progressWeightComponent);